@font-face{font-family:"Lato-Regular";src:url("../../assets/fonts/Lato-Regular.ttf")}@font-face{font-family:"Lato-Light";src:url("../../assets/fonts/Lato-Light.ttf")}@font-face{font-family:"Lato-LightItalic";src:url("../../assets/fonts/Lato-LightItalic.ttf")}@font-face{font-family:"Lato-Italic";src:url("../../assets/fonts/Lato-Italic.ttf")}*{font-family:"Lato-Regular"}body{background-color:#013c3c}.to-do-list{text-align:center;margin-top:100px}.section-title{display:flex;justify-content:center}.section-title .title{padding:10px;width:60%;font-size:4rem;color:#fff;background-color:rgba(139,139,139,.656);border-radius:5px}.title-background-image{clip-path:polygon(0% 100%, 100% 80%, 100% 0%, 0% 20%);background-image:url("../../images/backgrounds/forest.jpg");background-position:center;background-size:cover;background-attachment:fixed}p{font-size:1.5rem;color:rgba(230,230,230,.558);margin-top:40px}button{font-family:"Lato-Regular";font-size:1.7rem;font-weight:bold;padding:10px 20px;color:#fff;border:none;border-radius:5px;cursor:pointer;transition:background-color .5s ease}.input-container,.edit-task-container{display:flex;align-items:center;justify-content:center;gap:10px}.input-container{margin-top:50px}.add-button{background-color:#00b300;border-radius:100px}.add-button:hover{background-color:green}.filter-select{position:relative}.filter-select select{width:285px;font-size:1.7rem;font-weight:bold;padding:10px 20px;color:#fff;background-color:rgba(2,126,126,.3);border:none;border-radius:5px;cursor:pointer;transition:background-color .5s ease}.filter-select select:hover{background-color:rgba(0,199,199,.45)}.filter-select select option{background-color:#fff;color:#000;font-size:10px}.delete-button{background-color:#d31212}.delete-button:hover{background-color:#a40e0e}.move-button{background-color:#013c3c}.move-button:hover{background-color:rgba(1,60,60,.773)}.edit-button{background-color:rgba(0,0,0,0)}input[type=text]{font-size:1.7rem;padding:10px;border:2px solid rgba(204,204,204,.5);border-radius:5px;color:rgba(0,0,0,.703)}ol{padding:0;display:flex;flex-direction:column;align-items:center}li{width:85%;font-size:2rem;font-weight:bold;padding:15px;background-color:#f7f7f7;margin-bottom:10px;border:3px solid rgba(217,217,217,.75);border-radius:5px;display:flex;align-items:center;justify-content:space-between;line-height:3rem}.my-checkbox{width:20px;height:20px;cursor:pointer;appearance:none;border:2px solid rgba(0,0,0,.5);border-radius:5px}.my-checkbox:hover{border-color:#000}.my-checkbox:checked{border-color:#000}.my-checkbox:checked::before{content:"✔";color:#000;font-size:16px;display:block;text-align:center;line-height:18px}.completed-task .text{transition:color .3s ease;text-decoration:line-through rgba(0,0,0,.5);color:rgba(0,0,0,.237)}.text{flex:1;word-break:break-word;max-width:60%}.delete-button,.move-button,.edit-button,.save-button,.cancel-button,#myInputEditTask{padding:8px 12px;font-size:1.4rem;margin-left:10px;line-height:1.5rem}